var _ = Package.underscore._,
    package_name = "telescope:subscribe-to-posts",
    namespace = "telescope:subscribe-to-posts";

if (package_name != "project") {
    namespace = TAPi18n.packages[package_name].namespace;
}
if(_.isUndefined(TAPi18n.translations["cs"])) {
  TAPi18n.translations["cs"] = {};
}

if(_.isUndefined(TAPi18n.translations["cs"][namespace])) {
  TAPi18n.translations["cs"][namespace] = {};
}

_.extend(TAPi18n.translations["cs"][namespace], {"subscribed_posts":"Odebírané příspěvky","subscribe_to_thread":"Odebírat komentáře","unsubscribe_from_thread":"Odhlásit se z odběru komentářů"});
TAPi18n._registerServerTranslator("cs", namespace);
