var _ = Package.underscore._,
    package_name = "telescope:subscribe-to-posts",
    namespace = "telescope:subscribe-to-posts";

if (package_name != "project") {
    namespace = TAPi18n.packages[package_name].namespace;
}
if(_.isUndefined(TAPi18n.translations["es"])) {
  TAPi18n.translations["es"] = {};
}

if(_.isUndefined(TAPi18n.translations["es"][namespace])) {
  TAPi18n.translations["es"][namespace] = {};
}

_.extend(TAPi18n.translations["es"][namespace], {"subscribed_posts":"Posts suscritos","subscribe_to_thread":"Suscribirse a esta conversación","unsubscribe_from_thread":"Cancelar la suscripción a esta conversación"});
TAPi18n._registerServerTranslator("es", namespace);
