var _ = Package.underscore._,
    package_name = "telescope:subscribe-to-posts",
    namespace = "telescope:subscribe-to-posts";

if (package_name != "project") {
    namespace = TAPi18n.packages[package_name].namespace;
}
if(_.isUndefined(TAPi18n.translations["pl"])) {
  TAPi18n.translations["pl"] = {};
}

if(_.isUndefined(TAPi18n.translations["pl"][namespace])) {
  TAPi18n.translations["pl"][namespace] = {};
}

_.extend(TAPi18n.translations["pl"][namespace], {"subscribed_posts":"Posty które subskrybujesz","subscribe_to_thread":"Subskrybuj","unsubscribe_from_thread":"Nie subskrybuj"});
TAPi18n._registerServerTranslator("pl", namespace);
