var _ = Package.underscore._,
    package_name = "telescope:subscribe-to-posts",
    namespace = "telescope:subscribe-to-posts";

if (package_name != "project") {
    namespace = TAPi18n.packages[package_name].namespace;
}
if(_.isUndefined(TAPi18n.translations["pt-BR"])) {
  TAPi18n.translations["pt-BR"] = {};
}

if(_.isUndefined(TAPi18n.translations["pt-BR"][namespace])) {
  TAPi18n.translations["pt-BR"][namespace] = {};
}

_.extend(TAPi18n.translations["pt-BR"][namespace], {"subscribed_posts":"Postagens inscritas","subscribe_to_thread":"Inscreva-se aos comentários","unsubscribe_from_thread":"Desinscreva-se dos comentários"});
TAPi18n._registerServerTranslator("pt-BR", namespace);
