var _ = Package.underscore._,
    package_name = "telescope:subscribe-to-posts",
    namespace = "telescope:subscribe-to-posts";

if (package_name != "project") {
    namespace = TAPi18n.packages[package_name].namespace;
}
if(_.isUndefined(TAPi18n.translations["sv"])) {
  TAPi18n.translations["sv"] = {};
}

if(_.isUndefined(TAPi18n.translations["sv"][namespace])) {
  TAPi18n.translations["sv"][namespace] = {};
}

_.extend(TAPi18n.translations["sv"][namespace], {"subscribed_posts":"Bevakade inlägg","subscribe_to_thread":"Prenumerera på kommentarstråd","unsubscribe_from_thread":"Avprenumerera på kommentarstråd"});
TAPi18n._registerServerTranslator("sv", namespace);
