var _ = Package.underscore._,
    package_name = "telescope:subscribe-to-posts",
    namespace = "telescope:subscribe-to-posts";

if (package_name != "project") {
    namespace = TAPi18n.packages[package_name].namespace;
}
if(_.isUndefined(TAPi18n.translations["id"])) {
  TAPi18n.translations["id"] = {};
}

if(_.isUndefined(TAPi18n.translations["id"][namespace])) {
  TAPi18n.translations["id"][namespace] = {};
}

_.extend(TAPi18n.translations["id"][namespace], {"subscribed_posts":"Postingan yang Terlanggan","subscribe_to_thread":"Berlangganan komentar thread ini","unsubscribe_from_thread":"Berhenti berlangganan dari komentar thread ini"});
TAPi18n._registerServerTranslator("id", namespace);
